.leaderboard-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
  padding-bottom: 100px;
}

.leaderboard-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  position: relative;
}

.back-button {
  position: absolute;
  left: 0;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateX(-3px);
}

.leaderboard-header h1 {
  font-size: 36px;
  color: white;
  font-weight: 800;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
}

.tab-menu {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 25px;
}

.tab-button {
  padding: 12px 28px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border-radius: 25px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.tab-button.active {
  background: white;
  color: #667eea;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.3);
}

.tab-button:hover:not(.active) {
  background: rgba(255, 255, 255, 0.3);
}

.my-rank-card {
  background: white;
  border-radius: 20px;
  padding: 25px;
  margin-bottom: 25px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
}

.my-rank-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 2px solid #f0f0f0;
}

.my-rank-label {
  font-size: 16px;
  color: #888;
  font-weight: 600;
}

.my-rank-number {
  font-size: 32px;
  font-weight: 800;
}

.my-stats {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.stat-label {
  font-size: 13px;
  color: #888;
  font-weight: 500;
}

.stat-value {
  font-size: 22px;
  font-weight: 700;
  color: #667eea;
}

.leaderboard-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 30px;
}

.loading {
  text-align: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
  padding: 40px;
}

.leaderboard-item {
  display: flex;
  align-items: center;
  gap: 15px;
  background: white;
  padding: 18px 20px;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.leaderboard-item:hover {
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.leaderboard-item.current-user {
  border: 3px solid #667eea;
  background: linear-gradient(135deg, #f0f4ff 0%, #faf5ff 100%);
  box-shadow: 0 8px 30px rgba(102, 126, 234, 0.3);
}

.rank-section {
  min-width: 50px;
  text-align: center;
}

.rank {
  font-size: 24px;
  font-weight: 800;
}

.player-info {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.player-avatar {
  font-size: 32px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  border-radius: 50%;
}

.player-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.player-name {
  font-size: 16px;
  font-weight: 700;
  color: #333;
}

.player-department {
  font-size: 13px;
  color: #888;
  font-weight: 500;
}

.player-stats {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 6px;
}

.level-badge {
  padding: 6px 14px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 700;
}

.points {
  font-size: 16px;
  font-weight: 700;
  color: #667eea;
}

.leaderboard-footer {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 16px 24px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.stat-icon {
  font-size: 24px;
}

.stat-text {
  font-size: 14px;
  font-weight: 600;
  color: #667eea;
}

@media (max-width: 768px) {
  .leaderboard-container {
    padding: 15px;
    padding-bottom: 80px;
  }

  .leaderboard-header h1 {
    font-size: 28px;
  }

  .back-button {
    padding: 8px 16px;
    font-size: 12px;
  }

  .tab-button {
    padding: 10px 20px;
    font-size: 13px;
  }

  .my-rank-card {
    padding: 20px;
  }

  .my-stats {
    flex-direction: row;
    gap: 15px;
  }

  .stat-value {
    font-size: 18px;
  }

  .leaderboard-item {
    padding: 15px;
    gap: 12px;
  }

  .rank-section {
    min-width: 40px;
  }

  .rank {
    font-size: 20px;
  }

  .player-avatar {
    width: 40px;
    height: 40px;
    font-size: 24px;
  }

  .player-name {
    font-size: 14px;
  }

  .player-department {
    font-size: 11px;
  }

  .level-badge {
    padding: 5px 12px;
    font-size: 11px;
  }

  .points {
    font-size: 14px;
  }

  .leaderboard-footer {
    flex-direction: column;
    gap: 12px;
  }

  .stat-card {
    padding: 12px 20px;
    justify-content: center;
  }
}
